@import '../../shared/variables.scss';

.target {
	border: 1px solid $accent;
	user-select: none;
	overflow: hidden;
}

.shake {
	animation: shake 200ms;
}

@keyframes shake {
	10%,
	90% {
		transform: translate3d(-1px, 0, 0);
	}

	20%,
	80% {
		transform: translate3d(2px, 0, 0);
	}

	30%,
	50%,
	70% {
		transform: translate3d(-4px, 0, 0);
	}

	40%,
	60% {
		transform: translate3d(4px, 0, 0);
	}
}
